.landingPromoWomen1{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    background: url("./images/balayage-3.jpg");
    background-repeat: no-repeat;
    background-size: contain;
  }
  

.datePickerCard__info{
  margin: 50px 25px;
  display: flex;
  flex-direction: column;
  width: 350px;
  height: 500px;
  align-items: flex-end;
  box-shadow: 0px 5px 10px #c801c4, 0px 10px 20px #01e8e8;
}



.promo1__contentCard{
  margin-top: 10px;
  padding: 5px;
  color: black;
  font-size: 20px;
  text-align: center;

}


.textCard > h1{
  font-size: 35px;
  color: #01e8e8;
  text-align: center;
  
}

.textCard > h2{
  font-size: 30px;
  color: #c801c4;
  text-align: center;

}
.textCard > h5{
  
  color: #ffffff;
  text-align: center;
  margin-bottom: -50px;

}

.extra__content{
  display: none;
  
}

.booking{
  height: 100vh;
}



@media only screen and (max-width: 768px) {

  .landingPromoWomen1{
      
      height: auto;
      
    }

  .datePickerCard__info{
    margin: 0;
    width: 100%;
    
  }  

  .promo1__contentCard{
    background: #ebebeb;
    box-shadow: 0px 5px 10px #c801c4, 0px 10px 20px #01e8e8;

  }

  .landingPromoWomen1 > .extra__content{
  background: url("./images/Balayage-caramelo-2.jpg");
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 300px;
  background-repeat: no-repeat;
  background-size: 100% 100%;

  
}
} 